import React, { Component } from "react";
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button';

class UserCreate extends Component {

    constructor(props){
        super(props)
    
        //setting up functions
        this.onChangeFirstName = this.onChangeFirstName.bind(this);
        this.onChangeMiddleName = this.onChangeMiddleName.bind(this);
        this.onChangeLastName = this.onChangeLastName.bind(this);
        this.onChangeGender = this.onChangeGender.bind(this);
        this.onChangeEmail = this.onChangeEmail.bind(this);
        this.onChangePhone = this.onChangePhone.bind(this);
        this.onSubmit = this.onSubmit.bind(this);

        //setting up state
        this.state = {
            first_name: '',
            middle_name: '',
            last_name: '',
            gender: '',
            email: '',
            phone: ''
        }
    }
     //functions
    onChangeFirstName(e){
        this.setState({first_name: e.target.value})
    }

    onChangeMiddleName(e){
        this.setState({middle_name: e.target.value})
    }
    
    onChangeLastName(e){
        this.setState({last_name: e.target.value})
    }

    onChangeGender(e){
        this.setState({gender: e.target.value})
    }

    onChangeEmail(e){
        this.setState({email: e.target.value})
    }

    onChangePhone(e){
        this.setState({phone: e.target.value})
    }

    onSubmit(e){
        e.preventDefault();

        console.log(`Creating user...`);
        console.log(`First name: ${this.state.first_name}`);
        console.log(`Middle name: ${this.state.middle_name}`);
        console.log(`Last name: ${this.state.last_name}`);
        console.log(`Gender: ${this.state.gender}`);
        console.log(`Email: ${this.state.email}`);
        console.log(`Phone: ${this.state.phone}`);

        this.setState(
            {
                first_name: '',
                middle_name: '',
                last_name: '',
                gender: '',
                email: '',
                phone: ''
            }
        )
    }

    //render
    render() {
        return (
            <div class="form-wrapper">
                <p>User creation component</p>
                <Form>
                    <Form.Group controlId="Name">
                        <Form.Label>First Name</Form.Label>
                        <Form.Control type="text"/>
                    </Form.Group>

                    <Form.Group controlId="Name">
                        <Form.Label>Middle Name</Form.Label>
                        <Form.Control type="text"/>
                    </Form.Group>

                    <Form.Group controlId="Name">
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control type="text"/>
                    </Form.Group>

                    <Form.Group controlId="Name">
                        <Form.Label>Gender</Form.Label>
                        <Form.Control type="text"/>
                    </Form.Group>

                    <Form.Group controlId="Email">
                        <Form.Label>Email</Form.Label>
                        <Form.Control type="email"/>
                    </Form.Group>

                    <Form.Group controlId="Name">
                        <Form.Label>Phone</Form.Label>
                        <Form.Control type="text"/>
                    </Form.Group>

                    <Button variant="danger" size="lg" block="block" type="submit">Create User</Button>
                </Form>
            </div>
        );
    }
}

export default UserCreate